@import url("https://fonts.googleapis.com/css2?family=Inter:wght@700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inknut+Antiqua:wght@600;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inria+Sans:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inknut-antiqua: "Inknut Antiqua";
  --font-inter: Inter;
  --font-inria-sans: "Inria Sans";
  --font-rubik: Rubik;

  /* font sizes */
  --font-size-3xs: 0.63rem;
  --font-size-3xl: 1.38rem;
  --font-size-xl: 1.25rem;
  --font-size-mid-6: 1.1rem;
  --font-size-xs-9: 0.74rem;
  --font-size-smi-5: 0.78rem;
  --font-size-2xs: 0.69rem;
  --font-size-mini: 0.94rem;
  --font-size-6xl: 1.56rem;
  --font-size-mid: 1.06rem;
  --font-size-lg: 1.13rem;
  --font-size-sm: 0.88rem;
  --font-size-11xl: 1.88rem;
  --font-size-23xl: 2.63rem;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #130d0d;
  --color-gray-200: rgba(30, 30, 30, 0.94);
  --color-gray-300: rgba(0, 0, 0, 0.88);
  --color-gray-400: rgba(255, 255, 255, 0.54);
  --color-gainsboro-100: #d9d9d9;
  --color-gainsboro-200: #d6d9dd;
  --color-black: #000;
  --color-coral: #f26d2d;
  --color-mediumslateblue-100: #8978f5;
  --color-snow: #fffcfc;
  --color-midnightblue: rgba(35, 10, 98, 0.29);
  --color-silver: rgba(184, 189, 197, 0.58);
  --color-dimgray: #676d75;
  --color-darkred: #9a0a0a;
  --color-darkviolet: #a600ff;
  --color-darkslateblue: rgba(98, 73, 161, 0.29);
  --color-lightgray: #cbd4df;
  --brand-2: #f9f9f9;
  --color-whitesmoke-100: #f3f5f8;

  /* Border radiuses */
  --br-96xl: 115px;
  --br-92xl: 111px;
  --br-11xl-5: 30.5px;
  --br-12xl-5: 31.5px;
  --br-6xl-8: 25.8px;
  --br-51xl: 70px;
  --br-93xl: 112px;
  --br-9xs: 4px;
  --br-12xl: 31px;
}
